/**
 * E.g. 
 *  payload: {
 *      first_name: {
 *          person: "Andrew"
 *      },
 *      last_name: {
 *          person: "Mfune"
 *      },
 *      gender: {
 *          person: "F"
 *      }
 *  }
 * 
 *  tag: "person"
 * 
 *  result: {
 *      person: {
 *          first_name: "Andrew",
 *          last_name: "Mfune",
 *          gender: "F"
 *      }
 *  }
 * @param payload 
 * @param tag 
 * @returns 
 */
export function groupByTag(payload: Record<string, any>, tag: string) {
    const data = Object.values(Object.values(payload))
    const values: any = data.filter((data: any) => {
            return data[tag] ? true : false
        })
        .reduce((finalPayload: any, val: any) => {
            if (Array.isArray(val[tag])) {
                finalPayload[tag] = val[tag]
            } else {
                finalPayload[tag] = {...finalPayload[tag],...val[tag]} 
            }
            return finalPayload
        }, {})
    return values
}